@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@200&display=swap);
body {
  margin: 0;
  font-family: "Raleway", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/*Variables de diseño*/
:root {
  --color-white: #fbfbfb;
  --color-black: black;
  --color-black-out: #1a1a1a;
  --color-grey: #ebebeb;
}

.container-principal {
  display: grid;
  grid-template-rows: 50px 120px 70px 80% 770px 670px 350px 300px 300px 50px;
  grid-template-areas: "register" "search" "menuBar" "carousel" "gallery" "sellings" "offers" "brands" "contact" "footer";
  height: 100vh;
  box-sizing: border-box;
  overflow-x: hidden;
  background-color: #fbfbfb;
  background-color: var(--color-white);
}

.principal-register {
  grid-area: register;
  background-color: black;
  background-color: var(--color-black);
  color: #fbfbfb;
  color: var(--color-white);
}

.principal-searchBar {
  grid-area: search;
  background-color: #ebebeb;
  background-color: var(--color-grey);
}

.principal-menu {
  grid-area: menuBar;
}

.principal-carousel {
  grid-area: carousel;
}

.principal-discountGallery {
  grid-area: gallery;
}

.principal-bestSellingsFlowers {
  grid-area: sellings;
}

.principal-exclusiveOffers {
  grid-area: offers;
}

.principal-brands {
  grid-area: brands;
}

.principal-contact {
  grid-area: contact;
  background-color: #1a1a1a;
  background-color: var(--color-black-out);
  color: #fbfbfb;
  color: var(--color-white);
}

.principal-footer {
  grid-area: footer;
  background-color: black;
  background-color: var(--color-black);
  color: #fbfbfb;
  color: var(--color-white);
}

.p-footer {
    font-size: 12px;
    margin-top: 1em;
    margin-left: 12em;
}

